import moment from 'moment';
import _ from 'lodash';

import SuperAgent from '../models/SuperAgent';
import Agent from '../models/Agent';
import Player from '../models/Player';

import updatePlayerStatusAfterOrder from '../updateDB/utils/updatePlayerStatusAfterOrder'

import express from 'express';
const router = express.Router();

router.get('/push', async (req, res) => {

	const agent = await Agent.findOne({ 'account.username': 'diamond' })
	const players = await Player.find({ agent: agent._id }, '_id')

	players.forEach(player => {
		agent.players.push(player)
	})

	await agent.save()
	console.log(players)
	res.json(agent)

})

router.get('/setup', async (req, res) => {
	const players = await Player.find({})
	await Promise.all(players.map(async player => {
		await updatePlayerStatusAfterOrder(player._id)
	})).then(() => {
		res.json('done')
	})
})


router.get('/create-player', async (req, res) => {
	const superAgent = await SuperAgent.findOne({ 'account.username': 'boss' })
	const agent = await Agent.findOne({'account.username': 'diamond'})
	let newPlayer = new Player({
		account: {
			username: 'f',
			password: '1234',
			passcode: '4321',
			activate: true,
		},
		superAgent: superAgent._id,
		agent: agent._id
	})
	const player = await newPlayer.save().then(async newOne => {
		await updatePlayerStatusAfterOrder(newOne._id)
	})
	// await agent.players.push(player._id)
	// await agent.save()
	// await superAgent.players.push(player._id)
	// await superAgent.save()

	res.json(player)
})


router.get('/create-agent', async (req, res) => {
	const superAgent = await SuperAgent.findOne({ 'account.username': 'boss' })
	if(!_.isEmpty(superAgent)){
		let newAgent = new Agent({
			account: {
				username: 'diamond',
			}
		})
		const agent = await newAgent.save()
//		superAgent.agents.push(agent._id)
//		await superAgent.save()
		res.json('done')
	}else{
		res.json('super agent not found')
	}
})


router.get('/create-super-agent', (req, res) => {
	let newSuperAgent = new SuperAgent({
		account: {
			username: 'boss',
			password: '1234',
			passcode: '4321',
			activate: true
		}
	})
	newSuperAgent.save().then(() => { 
		console.log('saved superAgent') 
		res.json('saved superAgent')
	}).catch(err => { throw err })
})

router.get('/agent-info', (req, res) => {
	Agent.findOne({ 'account.username': 'diamond' }).populate('players').then(agent => {
		res.json(agent)
	})
})

import { Result } from '../models/EventOdd';

router.get('/create-result', (req, res) => {

	let newResult = new Result({

			"eventResultId" : "pickMon_College_GAME_1414713",
			"uniqueId": "221_222_FOOTBALL_GAME_08312017",
		    "sport" : "Football",
		    "oddType" : "Game",
		    "league" : "College",
		    "region" : "",
		    "details" : "Presbyterian College At Wake Forest",
		    "matchTime" : moment.utc("2017-08-31T22:30:00.000Z"),
		    "expireAt" : moment.utc("2017-09-03T11:00:00.000Z"),
		    "status" : "Finished",
		    "score" : {
		        "awayScore" : 51,
		        "homeScore" : 7
		    },
		    "team" : {
		        "awayROT" : "222",
		        "away" : "Wake Forest",
		        "homeROT" : "221",
		        "home" : "Presbyterian College"
		    },
		    "source" : {
		        "provider" : "custom",
		        "id" : "0"
		    }
		})

	Result.findOne({ uniqueId: newResult.uniqueId }).then(result => {
		if(_.isEmpty(result)){
			newResult.save().then(() => { res.json('done')})
		}else{
			res.json('existed')
		}
	})

	

})



export default router;
























